import {
  pluginName
} from './constants';

const CKEDITOR = window.CKEDITOR;

function getFirstTextNode(element) {
  const children = element.getChildren();
  for (let i = 0; i < children.count(); i++) {
    const child = children.getItem(i);
    if (child.type === CKEDITOR.NODE_TEXT) {
      return child;
    }
  }
  return null;
}

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init: function (editor) {
    const pluginConfig = CKEDITOR.tools.get_plugin_config(pluginName, editor);
    const colors = pluginConfig;

    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init: function () {
        this.add('default', 'Remove Color', 'Remove Color'); // Remove color default option
        console.log('colors', colors);
        for (let label in colors) {
          const colorValue = colors[label];
          this.add(colorValue, label, label);
        }
      },
      onRender: function () {
        this.setValue(''); // Prevents any item from being auto-selected
      },
      onClick: function (value) {
        editor.focus();
/*         if (value === 'default') {
          const selection = editor.getSelection();
          const ranges = selection.getRanges();

          editor.fire('lockSnapshot');

          console.log('ðŸŸ¡ Starting color removal...');

          // Force span splitting by applying and removing a dummy style
          const dummyStyle = new CKEDITOR.style({ element: 'span', styles: { 'background-color': '#ffffff' } });
          editor.applyStyle(dummyStyle);
          editor.removeStyle(dummyStyle);

          const bookmarks = selection.createBookmarks();
          ranges.forEach((range, i) => {
            console.log(`ðŸŸ¢ Range ${i + 1}:`, range);

            let found = 0;
            let unwrapped = 0;

            const tryProcessSpan = (span) => {
              if (
                span &&
                span.type === CKEDITOR.NODE_ELEMENT &&
                span.getName() === 'span' &&
                span.getStyle('color')
              ) {
                found++;
                console.log('ðŸŽ¯ Found span with color:', span.getOuterHtml());
                span.removeStyle('color');

                if (!span.hasAttributes()) {
                  const children = span.getChildren().toArray();
                  for (let child of children) {
                    span.insertBeforeMe(child.remove());
                  }
                  span.remove();
                  unwrapped++;
                  console.log('ðŸ§¹ Unwrapped span.');
                }
              }
            };

            // 1. Try enclosed node
            const enclosed = range.getEnclosedNode();
            tryProcessSpan(enclosed);

            // 2. Fallback: walker inside the range
            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return node.type === CKEDITOR.NODE_ELEMENT &&
                    node.getName() === 'span' &&
                    node.getStyle('color');
            };

            let node;
            while ((node = walker.next())) {
              tryProcessSpan(node);
            }

            // 3. NEW: Also check if startContainer or endContainer is a colored span
            tryProcessSpan(range.startContainer);
            tryProcessSpan(range.endContainer);

            console.log(`âœ… Range ${i + 1} done: found ${found}, unwrapped ${unwrapped}`);
          });

          editor.fire('unlockSnapshot');

          console.log('ðŸŽ‰ Finished removing color from selection.');
        } */
        if (value === 'default') {
          const selection = editor.getSelection();
          if (!selection) {
            console.warn('âš ï¸ No selection found.');
            return;
          }

          const bookmarks = selection.createBookmarks();
          const ranges = selection.getRanges();

          if (!ranges.length || ranges.every(r => r.collapsed)) {
            console.warn('âš ï¸ No text selected.');
            return;
          }

          editor.fire('lockSnapshot');
          console.log('ðŸŸ¡ Starting precise color removalâ€¦');

          ranges.forEach((range, index) => {
            if (range.collapsed) {
              console.log(`â­ Skipping collapsed range ${index + 1}`);
              return;
            }

            console.log(`ðŸŸ¢ Processing range ${index + 1}`);
            range.enlarge(CKEDITOR.ENLARGE_INLINE);

            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return (
                node.type === CKEDITOR.NODE_ELEMENT &&
                node.getName() === 'span' &&
                node.getStyle('color')
              );
            };

            let node;
            let spanCount = 0, cleanedCount = 0, unwrappedCount = 0;
            let foundAny = false;

            while ((node = walker.next())) {
              if (!node || !node.getStyle('color')) continue;
              foundAny = true;
              spanCount++;

              const nodeRange = editor.createRange();
              nodeRange.selectNodeContents(node);

              const startSpan = range.startContainer.getAscendant('span', true);
              const endSpan = range.endContainer.getAscendant('span', true);

              const needsSplit =
                startSpan &&
                endSpan &&
                startSpan.equals(node) &&
                endSpan.equals(node) &&
                !range.equals(nodeRange);

              if (needsSplit) {
                console.log('âœ‚ï¸ Splitting span partially covered by selection.');

                const startRange = editor.createRange();
                startRange.setStart(range.startContainer, range.startOffset);
                startRange.setEndAfter(node);

                if (typeof startRange.split === 'function') {
                  startRange.split();
                } else {
                  console.warn('startRange.split is not a function');
                }

                const endRange = editor.createRange();
                endRange.setStart(range.endContainer, range.endOffset);
                endRange.setEndAfter(node);

                if (typeof endRange.split === 'function') {
                  endRange.split();
                } else {
                  console.warn('endRange.split is not a function');
                }

                let current = node.getNext();
                while (current) {
                  if (
                    current.type === CKEDITOR.NODE_ELEMENT &&
                    current.getName() === 'span' &&
                    current.getStyle('color') &&
                    current.getPrevious() &&
                    current.getPrevious().equals(node)
                  ) {
                    current.removeStyle('color');

                    if (!current.hasAttributes()) {
                      const children = current.getChildren().toArray();
                      for (let child of children) {
                        current.insertBeforeMe(child.remove());
                      }
                      current.remove();
                      unwrappedCount++;
                      console.log('ðŸ§¹ Unwrapped empty span after color removal.');
                    } else {
                      console.log('ðŸŽ¯ Removed color, kept other styles:', current.getAttribute('style'));
                    }
                    cleanedCount++;
                    break;
                  }
                  current = current.getNext();
                }
              } else {
                node.removeStyle('color');
                cleanedCount++;

                if (!node.hasAttributes()) {
                  const children = node.getChildren().toArray();
                  for (let child of children) {
                    node.insertBeforeMe(child.remove());
                  }
                  node.remove();
                  unwrappedCount++;
                  console.log('ðŸ§¹ Unwrapped empty span after color removal.');
                } else {
                  console.log('ðŸŽ¯ Removed color, kept other styles:', node.getAttribute('style'));
                }
              }
            }

            // ðŸ” Fallback: try to remove color from nearest span if none found by walker
            if (!foundAny) {
              const colorSpan = range.startContainer.getAscendant('span', true);

              if (
                colorSpan &&
                colorSpan.type === CKEDITOR.NODE_ELEMENT &&
                colorSpan.getStyle('color')
              ) {
                console.log('âš ï¸ Fallback: deep splitting color span to handle partial selection');

                const originalStyle = colorSpan.getAttribute('style') || '';
                const keptStyle = originalStyle
                  .split(';')
                  .map(s => s.trim())
                  .filter(s => s && !s.startsWith('color'))
                  .join('; ');

                const parent = colorSpan.getParent();
                const bookmark = range.createBookmark();

                // Extract contents of the span and process them
                const frag = colorSpan.extractContents();
                const fragWalker = new CKEDITOR.dom.walker(frag);
                fragWalker.evaluator = () => true;

                let node;
                while ((node = fragWalker.next())) {
                  if (node.type === CKEDITOR.NODE_TEXT) {
                    const textLength = node.getText().length;
                    const nodeRange = editor.createRange();
                    nodeRange.selectNodeContents(node);

                    if (nodeRange.intersects(range)) {
                      const nodeStart = 0;
                      const nodeEnd = textLength;

                      const selectionStart = range.startContainer.equals(node) ? range.startOffset : nodeStart;
                      const selectionEnd = range.endContainer.equals(node) ? range.endOffset : nodeEnd;

                      const fullText = node.getText();
                      const before = fullText.slice(0, selectionStart);
                      const middle = fullText.slice(selectionStart, selectionEnd);
                      const after = fullText.slice(selectionEnd);

                      const insertBefore = node.getPrevious() || node.getParent();

                      if (before) {
                        const beforeNode = new CKEDITOR.dom.text(before);
                        insertBefore.insertBeforeMe(beforeNode);
                      }

                      if (middle) {
                        const middleNode = new CKEDITOR.dom.text(middle);
                        const span = new CKEDITOR.dom.element('span');
                        if (keptStyle) span.setAttribute('style', keptStyle);
                        span.append(middleNode);
                        insertBefore.insertBeforeMe(span);
                      }

                      if (after) {
                        const afterNode = new CKEDITOR.dom.text(after);
                        insertBefore.insertBeforeMe(afterNode);
                      }

                      node.remove();
                    }
                  } else if (node.type === CKEDITOR.NODE_ELEMENT) {
                    // Recurse to clean up style on inline elements inside span
                    const style = node.getAttribute('style') || '';
                    const newStyle = style
                      .split(';')
                      .map(s => s.trim())
                      .filter(s => s && !s.startsWith('color'))
                      .join('; ');
                    if (newStyle) {
                      node.setAttribute('style', newStyle);
                    } else {
                      node.removeAttribute('style');
                    }
                  }
                }

                // Insert processed content back
                colorSpan.remove(); // fully remove colored span
                parent.insertBefore(frag, bookmark.startNode);
                cleanedCount++;

                // Restore selection
                range.moveToBookmark(bookmark);
                console.log('ðŸŽ¯ Deep-split fallback applied. Color removed from partial selection only.');
              }
            }

            console.log(`âœ… Finished range ${index + 1}: found=${spanCount}, cleaned=${cleanedCount}, unwrapped=${unwrappedCount}`);
          });

          selection.selectBookmarks(bookmarks);
          editor.fire('unlockSnapshot');
          console.log('ðŸŽ‰ Color removal complete.');
          return;
        }
        else {
          const style = new CKEDITOR.style({
            element: 'span',
            styles: { color: value }
          });
          editor.focus();
          editor.applyStyle(style);
        }
      }
    });
  }
});
